import * as React from "react";
import { Component } from "react";

interface Props {
  gameWidth: number;
  currentEnemy: {
    name: string;
    srcName: string;
  };
}

class Foreground extends Component<Props> {
  render() {
    const numGrassBlocks = Math.ceil(this.props.gameWidth / 70);

    return (
      <div className="Foreground">
        <img
          className="opponent player"
          src="/static/img/character/body.png"
          alt="שחקן"
        />
        <img
          className="opponent enemy"
          src={`/static/img/${this.props.currentEnemy.srcName}/body.png`}
          alt="אויב"
        />
        <div className="grass">
          {[...Array(numGrassBlocks)].map((_, i) => {
            return <img src="/static/img/grass.png" alt="אדמה" key={i} />;
          })}
        </div>
        <style jsx>{`
          .Foreground {
            position: absolute;
            width: 100%;
            left: 0;
            bottom: 0;
          }

          .Foreground img.opponent {
            width: 10rem;
            height: 10rem;
            margin: 0 1rem -0.2rem;
          }

          .Foreground img.opponent.enemy {
            margin: 0 1rem -0.2rem;
            float: right;
          }

          .Foreground .grass {
            width: 100%;
          }

          .Foreground .grass img {
            width: calc(100% / ${numGrassBlocks});
          }
        `}</style>
      </div>
    );
  }
}

export default Foreground;
